/*  I. Font settings
 * --------------------------------------- */ 
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Regular"), local("Montserrat-Regular"), url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald Medium"), local("Oswald-Medium"), url("../fonts/Oswald-Medium.woff2") format("woff2"), url("../fonts/Oswald-Medium.woff") format("woff"), url("../fonts/Oswald-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
/* II. Styles reset
 * --------------------------------------- */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  border: 0;
  outline: 0;
}

[hidden] {
  display: none;
}

button,
input {
  border: none;
  outline: none;
  border-radius: 0;
}

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em;
}

i {
  display: inline-block;
}

pre {
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
}

blockquote,
q {
  quotes: blockquote, q rgba(136, 34, 34, 0) blockquote, q rgba(136, 34, 34, 0);
}
blockquote:before, blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

ul, ol {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*  III. Default styles
 * --------------------------------------- */
h1, h2, h3, h4, h5, h6,
p {
  cursor: default;
}

.container, .container_xxl, .container_xl, .container_lg, .container_md, .container_sm {
  margin: 0 auto;
  padding: 0 1.2rem;
  max-width: 132rem;
}

.d-block {
  display: block;
}
.d-inline {
  display: inline;
}
.d-none {
  display: none;
}

.row {
  margin: 0 -1.2rem;
  display: flex;
  flex-wrap: wrap;
}

.scroll-hidden {
  padding-right: 17px;
  background-color: #f1f1f1;
  overflow: hidden;
}

.wrapper {
  margin: 0 auto;
  max-width: 1920px;
  overflow: hidden;
  position: relative;
}

address {
  font-style: normal;
}

html {
  font-size: 10px;
}

body {
  font-family: "Montserrat", "Helvetica", sans-serif;
  font-size: 18px;
}

.font_xlite {
  font-weight: 200;
}
.font_medium {
  font-weight: 500;
}
.font_semibold {
  font-weight: 600;
}
.font_bold {
  font-weight: 700;
}

.text_lite {
  color: #ffffff;
}
.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text_accent-prim {
  color: #C1272D;
}

a, a:visited {
  text-decoration: none;
  outline: none;
  color: #373737;
  transition: color 0.1s ease-in-out;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button,
input[type=submit],
input[type=reset],
input[type=button],
.btn {
  text-decoration: none;
  cursor: pointer;
  user-select: none;
}

.btn {
  padding: 1rem 2.6rem;
  display: inline-block;
  font-family: "Oswald", "Helvetica", sans-serif;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  border-radius: 0.3rem;
  background: none;
  overflow: hidden;
  transition: 0.35s;
}
.btn_primary {
  color: #ffffff;
  background-color: #C1272D;
}
.btn_primary:hover {
  background-color: #373737;
}

input:not(input[type=checkbox]),
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
}

input::-ms-clear {
  display: none;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

select::-ms-expand {
  display: none;
}

.has-bg {
  position: relative;
}

.bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.bg img {
  display: block;
  width: 100%;
  height: 100%;
}
.bg_cover img {
  object-fit: cover;
}
.bg_contain img {
  object-fit: contain;
}

