/* VI. Layout
 * --------------------------------------- */
/* Common
 * -------------------------*/
.preloader {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9999;
  background: #fff;
}
.preloader.fade-out {
  opacity: 0;
}

/* Sticky footer 
 * -------------------------*/
html,
body,
.wrapper {
  display: flex;
}

html {
  min-height: 100%;
}

body,
.wrapper {
  width: 100%;
}

.wrapper {
  flex-direction: column;
}

header,
footer {
  flex: none;
}

main {
  flex-grow: 1;
  background: #9f9f9f;
}

/* Header 
 * -------------------------*/
.logo {
  width: 100%;
  max-width: 20rem;
  max-height: 5.2rem;
  overflow: hidden;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 3rem;
  padding-right: 4.4rem;
  padding-bottom: 3rem;
}
.navbar__contacts {
  display: flex;
}
.navbar__contacts-left {
  margin-right: 4rem;
  display: flex;
  align-items: center;
}
.navbar__social {
  width: 40px;
  height: 41px;
}
.navbar__social:not(:first-child) {
  margin-left: 1.6rem;
}
.navbar__phone {
  margin-bottom: 0.5rem;
  font-family: "Oswald", "Helvetica", sans-serif;
  font-size: 22px;
  font-weight: 400;
  text-align: center;
  color: #242424;
}

/* Menu 
 * -------------------------*/
.menu-list {
  display: flex;
  align-items: center;
}
.menu-list > .menu-item {
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.menu-list > .menu-item:not(:first-child) {
  margin-left: 4rem;
}
.menu-list .current-menu-item > a,
.menu-list .current-menu-parent > a {
  color: #C1272D;
}
.menu-list .current-menu-parent::before,
.menu-list .current-menu-parent::after {
  background-color: #C1272D;
}
.menu-mobile {
  margin-left: auto;
  margin-right: auto;
}
.menu-mobile-trigger {
  justify-content: center;
  align-items: center;
  flex: none;
  display: none;
  width: 48px;
  height: 48px;
