// Переменные и миксины
@import '_partials/variables'
@import '_partials/mixins'

// Комментарии, шрифты, дефолтные стили
@import "comments.sass"
@import "fonts.sass"
@import "default.sass"

// Сетка
@import "grid.sass"

/* VI. Layout
 --------------------------------------- */

/* Common
 -------------------------*/

.preloader
  width: 100%
  height: 100%
  position: fixed
  top: 0
  right: 0
  bottom: 0
  left: 0
  z-index: 9999
  background: #fff

  &.fade-out
    opacity: 0

/* Sticky footer 
 -------------------------*/
html,
body,
.wrapper
  display: flex

html
  min-height: 100%

body,
.wrapper
  width: 100%

.wrapper
  flex-direction: column

header,
footer
  flex: none

main
  flex-grow: 1
  background: #9f9f9f

/* Header 
 -------------------------*/

.logo
  width: 100%
  max-width: 20rem
  max-height: 5.2rem
  overflow: hidden

.navbar
  display: flex
  justify-content: space-between
  align-items: center
  padding-top: 3rem
  padding-right: 4.4rem
  padding-bottom: 3rem

  &__contacts
    display: flex
    &-left
      margin-right: 4rem
      display: flex
      align-items: center

  &__social
    width: 40px
    height: 41px
    &:not(:first-child)
      margin-left: 1.6rem

  &__phone
    margin-bottom: .5rem
    font:
      family: $ff-secondary
      size: 22px
      weight: 400
    text-align: center
    color: $black

/* Menu 
 -------------------------*/
.menu
  &-list
    display: flex
    align-items: center
    &>.menu-item
      padding-top: 2rem
      padding-bottom: 2rem
      &:not(:first-child)
        margin-left: 4rem
    .current-menu-item > a,
    .current-menu-parent > a
      color: $primary-color
    .current-menu-parent::before,
    .current-menu-parent::after
      background-color: $primary-color
  &-mobile
    margin-left: auto
    margin-right: auto

  &-mobile-trigger
    justify-content: center
    align-items: center
    flex: none
    display: none
    width: 48px
    height: 48px
    border: solid 1px $primary-color
    border-radius: $main-radius
    background: none
    transition: $prim-transition
    &__inner
      position: relative
      width: 18px
      height: 16px
    &__item
      position: absolute
      left: 0
      width: 18px
      height: 2px
      background: $primary-color
      &:first-child
        top: 0
      &:nth-child(2)
        width: 10px
        top: 7px
      &:last-child
        bottom: 0px
    &:active
      background: $primary-color
      .menu-mobile-trigger__item
        background: $white

  &-mobile-close
    position: absolute
    top: 4.6rem
    right: 2.5rem
    z-index: 999
    width: 20px
    height: 20px
    justify-content: center
    align-items: center
    background: none
    display: none
    &__item
      position: absolute
      display: block
      width: 18px
      height: 3px
      border-radius: 2px
      background: $secondary-color
      &:first-child
        transform: rotate(45deg)
      &:last-child
        transform: rotate(-45deg)

  &-mobile-overl
    display: none
    position: fixed
    top: 0
    right: 0
    bottom: 0
    left: 0
    z-index: 990
    width: 100%
    height: 100%
    background: none
    opacity: 0

  &-item
    font:
      family: $ff-primary
      size: 14px
      weight: 600
    text-transform: uppercase
    transition: $link-transition
    a
      transition: $link-transition
    &>a
      &:active
        color: $primary-color

    &-has-children
      position: relative
      padding-right: 2rem
      cursor: pointer
      // &>a
      //   color: green
      &::after,
      &::before
        content: ''
        display: inline-block
        position: absolute
        top: calc( 50% - .1em )
        width: 1rem
        height: 2px
        border-radius: 2px
        background: $secondary-color
        transition: transform $prim-transition

      &::before
        right: calc( 1.6rem - 1rem )
        transform: rotate(45deg)

      &::after
        right: calc( 1.6rem - 1.6rem )
        transform: rotate(-45deg)

      &:active
        &>a
          color: $primary-color
        &::before,
        &::after
          background: $primary-color
        &::before
          transform: rotate(-45deg)
        &::after
          transform: rotate(45deg)

.sub-menu
  position: absolute
  top: 100%
  left: -3rem
  padding: 1.4rem 2rem 3rem 2rem
  width: 100rem
  max-width: max-content
  background: #F5F5F5
  overflow: hidden
  opacity: 0
  visibility: hidden
  z-index: 1

  .menu-item
    max-width: 25rem
    font-family: $ff-primary
    font-size: 16px
    font-weight: 500
    text-transform: none
    border-bottom: dashed 1px $separ-color

    a
      display: block
      padding: 1rem

    &:active
      background: $white

