/* Baner 
 -------------------------*/
.baner
  padding-top: 16rem
  padding-bottom: 16rem

  &__heading
    margin-left: auto
    margin-right: auto
    max-width: 94rem
    text-align: center
    h1
      color: $white

  
    

/* Form 
 -------------------------*/
.form
  font-size: 1.6rem
  font-family: $ff-primary
  font-weight: 500
  line-height: 24px
  color: $text-primary-color

  &-row
    display: flex
    .form__group:not(:first-child)
      margin-left: 2rem

  &__control
    padding: 1.8rem 2.3rem
    width: 100%
    font-family: $ff-primary
    font-size: 1.6rem
    font-weight: 500
    color: $text-primary-color
    background: $form-input-bg
    border: 1px solid #D9D9D9
    border-radius: 5px
    outline: none
    &:focus
      border-color: $input-focus-border-color

  &__label
    display: block
    margin-bottom: 1rem
    font-size: 1.6rem
    font-weight: 500
    line-height: 24px
    color: $text-primary-color
    cursor: default
    user-select: none
    &_lite
      color: $white
    &_type-file
      display: inline-flex
      align-items: center
      flex-wrap: wrap
      font-size: 1.8rem
      line-height: 2.4rem
      cursor: pointer
      &:hover
        text-decoration: underline
        .file-extname
          text-decoration: underline
      .file-extname
        color: #989898

  &__textarea
    padding: 1.8rem 2.3rem
    width: 100%
    min-height: 114px
    font-family: $ff-primary
    font-size: 1.6rem
    font-weight: 500
    color: $text-primary-color
    background: $form-input-bg
    border: 1px solid #D9D9D9
    border-radius: 5px
    outline: none
    &:focus
      border-color: $input-focus-border-color

  &__file
    position: absolute
    width: 0
    height: 0

    &_selected
      display: flex
      align-items: flex-start
      margin-top: 0.8rem
      margin-bottom: 0.5rem
      overflow: hidden
      &-lite
        color: $white
      &-label
        flex: none
        margin-right: 1rem
      &-value
        flex: 1
        color: #989898

  &-screp
    display: inline-block
    margin-right: 1rem
    width: 20px
    height: 20px
    background: url(../img/icon-screp.svg) center no-repeat
    background-size: contain
    &_red
      background: url(../img/icon-screp-red.svg) center no-repeat

  &__submit
    padding: 1.4rem 3.4rem
    font-size: 1.8rem
    flex: none
    &-row
      margin-top: 3rem
      display: flex
      align-items: center

  &__group
    width: 100%
    &_type-file
      position: relative

  &__agree-text
    margin-left: 2rem
    max-width: 36.5rem
    font-size: 1.4rem
    font-weight: 500
    line-height: 20px
    color: #989898

@import "form processing.sass"

/* Footer 
 -------------------------*/
.footer
  padding: 5rem
  background: #555555

.map
  &-placement
    position: relative
    padding-top: 10rem
    padding-bottom: 10rem
    height: 70rem
    &__map
      position: absolute
      width: 100%
      height: 100%
      top: 0
      right: 0
      bottom: 0
      left: 0
      z-index: 0
    &__interlayer
      display: none
      position: absolute
      width: calc( 100% - 52px )
      height: 100%
      top: 0
      right: 52px
      bottom: 0
      left: 0
      z-index: 10
    &__container
      position: relative
      z-index: 50
      // padding: 0
      margin-left: calc(50vw - 725px)
      display: inline-flex

.marker-class
  display: block
  width: 200px
  height: 200px
  background: red
  border: solid 5px #000

/* Contacts page - Map 
 -------------------------*/
.contacts-map
  position: relative
  z-index: 1
  height: 51rem
  border-radius: $main-radius
  overflow: hidden

  &-placement
    position: relative

  &-container
    padding-top: 2rem
    padding-bottom: 10rem

/* Modal 
 -------------------------*/
.modal
  width: 100%
  height: 100%
  position: fixed
  top: 0
  right: 0
  bottom: 0
  left: 0
  z-index: -1
  display: none
  justify-content: center
  align-items: center
  opacity: 0
  &.shown
    opacity: 1
    z-index: 9999
    .modal__inner
      // Транисформация для анимации
      transform: translate(0)
  &__inner
    display: flex
    flex-direction: column
    position: relative
    z-index: 100
    margin: 1.5rem
    padding: 7rem 5rem
    min-width: 290px
    max-width: 104rem
    min-height: 400px
    max-height: 80vmin
    border-radius: .5rem
    box-shadow: 0 5px 20px 2px rgba(0, 0, 0, .35)
    background: $white
    transform: translateY(25px)
    transition: inherit
  &__content
    display: flex
    flex-direction: column
    height: 100%
    overflow-x: hidden
    overflow-y: auto
  &__header
    padding-bottom: 0.75rem
  &__body
    padding-top: 0.75rem
    flex: 1
    // overflow-x: hidden
    // overflow-y: auto

  &-backdrop
    display: block
    position: absolute
    top: 0
    right: 0
    bottom: 0
    left: 0
    z-index: 1
    width: 100%
    height: 100%
    background: rgba(0, 0, 0, 0.8)

  &__close
    position: absolute
    top: -22px
    right: -22px
    cursor: pointer
    width: 20px
    height: 20px
    background: none
    &::before,
    &::after
      content: ''
      display: inline-block
      width: 100%
      height: 3px
      position: absolute
      left: 0
      right: 0
      background: $white
      border-radius: 4px
      transition: .2s
    &::before
      transform: rotate(45deg)
    &::after
      transform: rotate(-45deg)
    // &:hover
    //   &::before,
    //   &::after
    //     background: lighten(#000, 50%)

