/* II. Styles reset
 --------------------------------------- */

*,
*::before,
*::after
  margin: 0
  padding: 0
  box-sizing: border-box
  -webkit-tap-highlight-color: transparent

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video
  border: 0
  outline: 0
//    font-size: 100%
  vertical-align: baseline
  background: transparent

[hidden]
  display: none

button,
input
  border: none
  outline: none
  border-radius: 0

code, kbd, pre, samp
  font-family: monospace, serif
  font-size: 1em

i
  display: inline-block

pre
  white-space: pre
  white-space: pre-wrap
  word-wrap: break-word

blockquote,
q
  quotes: &#8220 &#8220
  &:before,
  &:after
    content: ''
    content: none

ul, ol
  list-style: none

table
  border-collapse: collapse
  border-spacing: 0

/*  III. Default styles
 --------------------------------------- */

//  1. Basic
// -------------------  

h1, h2, h3, h4, h5, h6,
p
  cursor: default

// .continer_xs не нужен - достаточно функций .container
.container
  &,
  &_xxl,
  &_xl,
  &_lg,
  &_md,
  &_sm
    margin: 0 auto
    padding: 0 $gutter
    max-width: 132rem

.d
  &-block
    display: block
  &-inline
    display: inline
  &-none
    display: none

.row
  margin: 0 (- $gutter)
  display: flex
  flex-wrap: wrap

.scroll-hidden
  // для скрытия полосы прокрутки
  padding-right: 17px
  background-color: #f1f1f1
  overflow: hidden

.wrapper
  margin: 0 auto
  max-width: 1920px
  overflow: hidden
  position: relative

//  2. Typography
// ------------------------- 
address
  font-style: normal

html
  font:
    size: 10px

body
  font:
    family: $ff-primary
    size: 18px
  //   weight: 400
  // line-height: 28px
  // color: $text-primary-color
