// Font weights
.font
  &_xlite
    font-weight: 200
  &_medium
    font-weight: 500
  &_semibold
    font-weight: 600
  &_bold
    font-weight: 700

// Text settings
.text
  &_lite
    color: $white
  &-left
    text-align: left
  &-center
    text-align: center
  &_accent-prim
    color: $text-accent-primary

//  3. Links
//  -------------------------

a
  &, &:visited
    text-decoration: none
    outline: none
    color: $link-color
    transition: color $link-transition ease-in-out

