//   4. Images
//  -------------------------

img
  display: block
  max-width: 100%
  height: auto

//   5. Buttons
//  -------------------------

button,
input[type="submit"],
input[type="reset"],
input[type="button"],
.btn

  text-decoration: none
  cursor: pointer
  user-select: none

.btn
  padding: $btn-padding-y $btn-padding-x
  display: inline-block
  font:
    family: $ff-secondary
    size: 16px
    weight: 500
  text-transform: uppercase
  border-radius: $btn-radius
  background: none
  overflow: hidden
  transition: $prim-transition

  &_primary
    color: $btn-color
    background-color: $btn-prim-bg
    &:hover
      background-color: $btn-second-bg

  // &_secondary

    // &_success
